
          @import '@/assets/sass/variables.scss';
          @import '@/assets/sass/mixins.scss';
        

.public-transports-box {
  position: relative;

  .hide-box-content {
    display: none;
  }

  .public-transports-box-title {
    cursor: pointer;
    margin-bottom: 0;
  }

  .accordion-icon {
    position: absolute;
    top: 0;
    right: 0;
    margin-right: 17px;
    height: 15px;
    margin-top: 25px !important;
    cursor: pointer;
  }

  .hidden {
    position: absolute;
    top: -100px;
  }
  .public-transport-buttons {
    display: flex;
    gap: 10px;
    margin-top: 24px;
    margin-bottom: 10px;
  }

  .public-transports-button {
    display: flex;
    background-color: white;
    color: black;
    font-weight: 500;
    border: 1px solid lightgray;
    position: relative;

    .public-transports-bus {
      width: 18px;
      height: 18px;
      margin-right: 8px;
      transition: opacity 0.2s ease;
    }

    .default-icon {
      opacity: 1;
    }
    .hover-icon {
      position: absolute;
      left: 12px; // Ajustez selon votre padding
      opacity: 0;
    }

    &:hover,
    &.is-active {
      .default-icon {
        opacity: 0;
      }
      .hover-icon {
        opacity: 1;
      }
    }

    &.is-active {
      background-color: #337ab7;
      border: none;
      color: white;
    }
  }
}

@media only screen and (max-width: 600px) {
  .public-transport-buttons {
    flex-direction: column;

    .public-transports-button {
      margin-bottom: 10px;
    }
  }
}
